import React from "react";
import Navigation from "../components/Navbar";
import TopNavbar from "../components/Navbar/TopNavbar";
import Card from "../components/UI/Card";
import WelcomeCard from "../components/UI/WelcomeCard";
import styles from "./styles.module.css";
import {
  GiCheckMark,
  GiChart,
  GiChainedArrowHeads,
  GiChessKing,
} from "react-icons/gi";

const Home = () => {
  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <Navigation dashboard={styles.active} />
        <div className={styles.appContents}>
          <TopNavbar title="Dashboard" />
          <div className={styles.contents}>
            <div className={styles.contentGrid}>
              <div className={styles.left}>
                <WelcomeCard />
                <div className={styles.cardGrid}>
                  <Card
                    color={styles.purple}
                    name="My Appraisal Score"
                    score="10"
                  >
                    <GiCheckMark />
                  </Card>
                  <Card color={styles.cyan} name="Manager's Rating" score="10">
                    <GiChessKing />
                  </Card>
                  <Card color={styles.red} name="Overall Rating" score="10">
                    <GiChart />
                  </Card>
                  <Card color={styles.gold} name="HR's Calibration" score="10">
                    <GiChainedArrowHeads />
                  </Card>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
