import axios from "axios";
import {
  USER_BASIC_INFO_FAIL,
  USER_BASIC_INFO_REQUEST,
  USER_BASIC_INFO_SUCCESS,
  USER_EINFO_FAIL,
  USER_EINFO_REQUEST,
  USER_EINFO_SUCCESS,
  USER_OINFO_FAIL,
  USER_OINFO_REQUEST,
  USER_OINFO_SUCCESS,
  USER_PHOTO_FAIL,
  USER_PHOTO_REQUEST,
  USER_PHOTO_SUCCESS,
} from "../constants/profileConstants";

import {
  USER_ADD_ACCOUNT_FAIL,
  USER_ADD_ACCOUNT_SUCCESS,
  USER_DETAILS_SUCCESS,
  USER_ADD_ACCOUNT_REQUEST,
} from "../constants/userConstants";

export const updateBasicInfo =
  (
    firstname,
    middlename,
    lastname,
    email,
    dob,
    gender,
    state,
    mobile,
    address,
    description
  ) =>
  async (dispatch, getState) => {
    try {
      dispatch({ type: USER_BASIC_INFO_REQUEST });

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };
      const { data } = await axios.put(
        `/api/v1/staff`,
        {
          firstname,
          middlename,
          lastname,
          email,
          dob,
          gender,
          state,
          mobile,
          address,
          description,
        },
        config
      );

      dispatch({
        type: USER_BASIC_INFO_SUCCESS,
        payload: data.data,
      });
      dispatch({
        type: USER_DETAILS_SUCCESS,
        payload: data.data,
      });
    } catch (error) {
      dispatch({
        type: USER_BASIC_INFO_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

