import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { verify } from "../actions/userActions";
import img from "../assets/Hr.jpg";
import logo from "../assets/logo.png";
import Loading from "../components/UI/Loading";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import { FaDoorOpen } from "react-icons/fa";

const Register = ({ history }) => {
  const dispatch = useDispatch();

  const [email, setEmail] = useState("");

  const userStatus = useSelector((state) => state.userStatus);
  const { loading, error, emailVerification } = userStatus;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(verify(email));
  };

  useEffect(() => {
    if (emailVerification) {
      history.push("/verifyOTP");
    }
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [history, emailVerification, userInfo]);

  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <img src={logo} alt="Lotus Beta Analytics" />
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          <div className={styles.forms}>
            {error ? (
              <div className={`col-md-12 text-center ${styles.formPadding}`}>
                <small
                  className="alert alert-info"
                  style={{ padding: ".5rem", borderRadius: "10px" }}
                >
                  {error}
                </small>
              </div>
            ) : (
              <h5 className="text-center">Staff Registration</h5>
            )}
            {loading ? (
              <Loading />
            ) : (
              <form onSubmit={submitHandler}>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  {/* <label>Email Address</label> */}
                  <input
                    type="email"
                    className="form-control"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Enter Email Address"
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <input
                    type="submit"
                    className="form-control btn btn-warning"
                    value="Register"
                  />
                </div>
                <div
                  className={`col-md-12 ${styles.formPadding}`}
                  style={{ textAlign: "right" }}
                >
                  <Link to="/login">
                    <FaDoorOpen />
                    &nbsp;Login Here
                  </Link>
                </div>
                <div className={`col-md-12 ${styles.formPadding} text-center`}>
                  <small>
                    &copy; {new Date().getFullYear()} All Rights and Reserved
                    <br />
                    Lotus Beta Analytics Nigeria Limited
                  </small>
                </div>
              </form>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
