import React from "react";
import { Link } from "react-router-dom";
import styles from "./styles.module.css";

const Steps = (props) => {
  return (
    <div className={styles.steps}>
      <div>
        <div className={`${styles.step} ${props.binfo}`}>
          <Link to="/edit/profile/">Basic Information</Link>
        </div>
        <div className={styles.line}></div>
        <div className={`${styles.step} ${props.einfo}`}>
          <Link to="/edit/profile/emergency-info">Emergency Contact</Link>
        </div>
        <div className={styles.line}></div>
        <div className={`${styles.step} ${props.oinfo}`}>
          <Link to="/edit/profile/office-info">Official Information</Link>
        </div>{" "}
        <div className={styles.line}></div>
        <div className={`${styles.step} ${props.ainfo}`}>
          <Link to="/edit/profile/account-info">Account Information</Link>
        </div>{" "}
        <div className={styles.line}></div>
        <div className={`${styles.step} ${props.pinfo}`}>
          <Link to="/edit/profile/photo-upload">Photo Upload</Link>
        </div>
      </div>
    </div>
  );
};

export default Steps;
