import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import img from "../assets/Hr.jpg";
import logo from "../assets/logo.png";
import Loading from "../components/UI/Loading";
import styles from "./styles.module.css";
import { verifyOtp } from "../actions/userActions";

const VerifyOTP = ({ history }) => {
  const dispatch = useDispatch();
  const [code, setCode] = useState("");

  const userStatus = useSelector((state) => state.userStatus);
  const { emailVerification } = userStatus;

  const otpVerification = useSelector((state) => state.otpVerification);
  const { loading, error, success } = otpVerification;

  const submitHandler = (e) => {
    e.preventDefault();
    const email = emailVerification.staff.email;

    dispatch(verifyOtp(email, code));
  };

  useEffect(() => {
    if (!emailVerification) {
      history.push("/");
    }
    if (success) {
      history.push("/update/password");
    }
  }, [history, emailVerification, success]);

  //   useEffect(() => {
  //     if (emailVerification) {
  //       history.push("/auth");
  //     }
  //   }, [history, emailVerification]);

  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <img src={logo} alt="Lotus Beta Analytics" />
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          <p className="alert alert-success">
            Registration Successful An Email containing your One-Time-password
            has been sent to your Email Account!
          </p>
          {error ? (
            <div className="alert alert-info" style={{ padding: ".5rem" }}>
              {error}
            </div>
          ) : (
            ""
          )}
          {loading ? (
            <Loading />
          ) : (
            <div className={styles.forms}>
              <form onSubmit={submitHandler}>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <label>Authorization Code</label>
                  <input
                    type="text"
                    className="form-control"
                    onChange={(e) => setCode(e.target.value)}
                    placeholder="Enter OTP"
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <input
                    type="submit"
                    className="form-control btn btn-warning"
                    value="LOGIN"
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding} text-center`}>
                  <small>
                    &copy; {new Date().getFullYear()} All Rights and Reserved
                    Lotus Beta Analytics Nigeria Limited
                  </small>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default VerifyOTP;
