import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import logo from "../assets/logo.png";
import { update } from "../actions/userActions";
import Loading from "../components/UI/Loading";
import styles from "./styles.module.css";

const UpdatePassword = ({ history }) => {
  const dispatch = useDispatch();
  const [password, setPassword] = useState("");
  const [confirmpassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState("");

  const otpVerification = useSelector((state) => state.otpVerification);
  const { user } = otpVerification;

  const passwordUpdate = useSelector((state) => state.passwordUpdate);
  const { loading, error, success } = passwordUpdate;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const submitHandler = (e) => {
    e.preventDefault();
    const id = user.id;
    if (password === confirmpassword) {
      dispatch(update(id, password));
    } else {
      setMessage("Password does not match");
    }
  };
  useEffect(() => {
    if (!user) {
      history.push("/verifyOtp");
    }
    if (success && userInfo) {
      history.push("/dashboard");
    }
  }, [history, user, userInfo, success]);
  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <img src={logo} alt="Lotus Beta Analytics" />
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          {error ? <div className="alert alert-danger">{error}</div> : ""}
          {loading ? (
            <Loading />
          ) : (
            <div className={styles.forms}>
              {message && <p className="alert alert-info">{message}</p>}
              <form onSubmit={submitHandler}>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <input
                    type="password"
                    className="form-control"
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Enter Password"
                    value={password}
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <input
                    type="password"
                    className="form-control"
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    placeholder="Confirm Password"
                    value={confirmpassword}
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding}`}>
                  <input
                    type="submit"
                    className="form-control btn btn-warning"
                    value="LOGIN"
                  />
                </div>
                <div className={`col-md-12 ${styles.formPadding} text-center`}>
                  <small>
                    &copy; {new Date().getFullYear()} All Rights and Reserved
                    Lotus Beta Analytics Nigeria Limited
                  </small>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default UpdatePassword;
