import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import img from "../assets/Hr.jpg";
import logo from "../assets/logo.png";
import Loading from "../components/UI/Loading";
import styles from "./styles.module.css";
import { login } from "../actions/userActions";

const Verify = ({ history }) => {
  const dispatch = useDispatch();
  const [password, setPassword] = useState("");

  const userStatus = useSelector((state) => state.userStatus);
  const { emailVerification } = userStatus;
  const email = emailVerification.data;

  const userLogin = useSelector((state) => state.userLogin);
  const { loading, error, userInfo } = userLogin;

  useEffect(() => {
    if (emailVerification.auth === false) {
      history.push("/update-password");
    }
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [history, emailVerification, userInfo]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(login(email, password));
  };

  //   useEffect(() => {
  //     if (emailVerification) {
  //       history.push("/auth");
  //     }
  //   }, [history, emailVerification]);

  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.loginGrid}>
          <div className={`${styles.loginCard} ${styles.img}`}>
            <img src={img} alt="Staff Portal" />
          </div>
          <div className={styles.loginCard}>
            <div className={styles.logo}>
              <img src={logo} alt="Lotus Beta Analytics" />
            </div>
            <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
            <h6 className="text-center">Employee's Portal</h6>
            {error ? <div className="alert alert-danger">{error}</div> : ""}
            {loading ? (
              <Loading />
            ) : (
              <div className={styles.forms}>
                <form onSubmit={submitHandler}>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <label>Password</label>
                    <input
                      type="password"
                      className="form-control"
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder="Enter Password"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="submit"
                      className="form-control btn btn-warning"
                      value="LOGIN"
                    />
                  </div>
                  <div
                    className={`col-md-12 ${styles.formPadding} text-center`}
                  >
                    <small>
                      &copy; {new Date().getFullYear()} All Rights and Reserved
                      Lotus Beta Analytics Nigeria Limited
                    </small>
                  </div>
                </form>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Verify;
