import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import states from "./state";
import styles from "../screens/styles.module.css";
import { updateEInfo } from "../actions/profileActions";
import Loading from "./UI/Loading";

const EmergencyInfo = ({ user, history }) => {
  const dispatch = useDispatch();
  const einfo = useSelector((state) => state.einfo);
  const { loading, error, success } = einfo;

  const [emergencyContactName, setEmergencyContactName] = useState(
    user && user.emergencyContactName
  );
  const [emergencyContactEmail, setEmergencyContactEmail] = useState(
    user && user.emergencyContactEmail
  );
  const [emergencyContactState, setEmergencyContactState] = useState(
    user && user.emergencyContactState
  );
  const [emergencyContactPhone, setEmergencyContactPhone] = useState(
    user && user.emergencyContactPhone
  );
  const [emergencyContactRelationship, setEmergencyContactRelationship] =
    useState(user && user.emergencyContactRelationship);
  const [emergencyContactAddress, setEmergencyContactAddress] = useState(
    user && user.emergencyContactAddress
  );

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateEInfo(
        emergencyContactName,
        emergencyContactEmail,
        emergencyContactState,
        emergencyContactPhone,
        emergencyContactRelationship,
        emergencyContactAddress
      )
    );
  };

  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && (
        <div className="alert alert-success">Emergency Info Saved</div>
      )}
      {loading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={submitHandler}>
            <div className={`row`}>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Emergency Contact's Name</label>
                <input
                  type="text"
                  className="form-control"
                  value={emergencyContactName}
                  onChange={(e) => setEmergencyContactName(e.target.value)}
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label> Emergency Contact's Email</label>
                <input
                  type="email"
                  className="form-control"
                  value={emergencyContactEmail}
                  onChange={(e) => setEmergencyContactEmail(e.target.value)}
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Emergency Contact's Phone</label>
                <input
                  type="tel"
                  className="form-control"
                  value={emergencyContactPhone}
                  onChange={(e) => setEmergencyContactPhone(e.target.value)}
                />
              </div>

              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>State of Origin</label>
                <select
                  className="form-control"
                  value={emergencyContactState}
                  onChange={(e) => setEmergencyContactState(e.target.value)}
                >
                  <option>
                    {emergencyContactState
                      ? emergencyContactState
                      : "Select....."}
                  </option>
                  {states.map((item) => (
                    <option value={item} key={item}>
                      {item}
                    </option>
                  ))}
                </select>
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Emergency Contact Relationship</label>
                <input
                  type="text"
                  className="form-control"
                  value={emergencyContactRelationship}
                  onChange={(e) =>
                    setEmergencyContactRelationship(e.target.value)
                  }
                />
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label>Emergency Contact Address</label>
                <textarea
                  className="form-control"
                  onChange={(e) => setEmergencyContactAddress(e.target.value)}
                >
                  {emergencyContactAddress}
                </textarea>
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label />
                <input
                  type="submit"
                  className="btn btn-dark form-control"
                  value="Save"
                />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default EmergencyInfo;
