import axios from "axios";
import {
  GET_ALL_STAFF_REQUEST,
  GET_ALL_STAFF_SUCCESS,
  GET_ALL_STAFF_FAIL,
} from "../constants/adminConstants";

export const getAllStaffs = (id) => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_ALL_STAFF_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(`/api/v1/staff/`, config);

    dispatch({
      type: GET_ALL_STAFF_SUCCESS,
      payload: data.data,
    });
  } catch (error) {
    dispatch({
      type: GET_ALL_STAFF_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
