import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from 'react-router-dom'
import styles from './styles.module.css'
import logo from "../../assets/purple.png";
import { logout } from "../../actions/userActions";
import { FaUserCircle, FaTachometerAlt, FaUsers, FaLayerGroup, FaRegChartBar, FaPowerOff } from "react-icons/fa";
import { useHistory } from "react-router";
import { GiAbstract050 } from "react-icons/gi";


const Navigation = (props) => {
    const dispatch = useDispatch();

    const [mobile, setMobile] = useState(false)
    let history = useHistory();

    const userDetails = useSelector((state) => state.userDetails);
    const { user } = userDetails;

    const logouthandler = () => {
        dispatch(logout());
        history.push("/")
    }

    const expand = () => {
        setMobile(true)
    }
    const collapse = () => {
        setMobile(false)
    }

    return (
        <>
            <div className={styles.icon}>
                <button className="btn btn-dark" onClick={expand}><GiAbstract050 /></button>
            </div>

            <div className={mobile ? styles.mobilenavigation : styles.navigation}>
                <div className={styles.closeicon}>
                    <button className="btn btn-dark" onClick={collapse}> Close Navigation</button>
                </div>
                <div className={`${styles.logo} text-center`}>
                    <img src={logo} alt="Lotus Beta Analytics" />
                    <small>Lotus Beta Analytics</small>
                </div>
                <div className={styles.profile}>
                    <div className={styles.photo}>
                        <img src={`/uploads/${user ? user.photo : ""}`} alt={user && user.firstname} />
                    </div>
                    <small>{user && user.firstname}</small><br />

                </div>
                <div className={styles.url}>
                    <ul>
                        <Link to="/dashboard"><li className={props.dashboard}><FaTachometerAlt />Dashboard</li></Link>
                        <Link to="/profile"><li className={props.profile}><FaUserCircle />Profile</li></Link>
                        <Link to="/appraisal"><li className={props.appraisal}><FaRegChartBar />Appraisal</li></Link>
                        <Link to="/myteam"><li className={props.team}><FaUsers />My Team</li></Link>
                        {user && user.role === "Admin" ? <Link to="/users"><li className={props.staff}><FaUsers />All Staffs</li></Link> : ""}
                        <Link to="/init"><li><FaLayerGroup />Set Initiatives</li></Link>
                        <Link to="/#logout" onClick={logouthandler}><li><FaPowerOff />Logout</li></Link>
                    </ul>
                </div>

            </div>
        </>
    )
}

export default Navigation
