import React, { useState, useEffect } from "react";
//import { useSelector, useDispatch } from "react-redux";
import styles from "../screens/styles.module.css";
import Loading from "./UI/Loading";
import axios from "axios";

const DisplayPhoto = ({ user, history }) => {
  //const dispatch = useDispatch();
  //   const ainfo = useSelector((state) => state.ainfo);
  //   const { loading, error, success } = ainfo;

  useEffect(() => {
    if (!user) {
      history.push("/profile");
    }
  }, [user, history]);

  const [uploading, setUploading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  const uploadFileHandler = async (e) => {
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append("file", file);
    setUploading(true);

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    try {
      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      await axios.post("/api/v1/staff/uploa", formData, config);

      setSuccess(true);
      setUploading(false);
    } catch (error) {
      setError(error.response.data.error);
      setUploading(false);
    }
  };
  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && (
        <div className="alert alert-success">Photo Upload success</div>
      )}
      {uploading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={uploadFileHandler} encType="multipart/form-data">
            <div className={`row`}>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Click to upload Picture</label>
                <input type="file" onChange={uploadFileHandler} />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default DisplayPhoto;
