import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import states from "./state";
import styles from "../screens/styles.module.css";
import { updateBasicInfo } from "../actions/profileActions";
import Loading from "./UI/Loading";

const BasicInfo = ({ user, history }) => {
  const dispatch = useDispatch();

  const info = useSelector((state) => state.info);
  const { loading, error, success } = info;

  useEffect(() => {
    if (!user) {
      history.push("/profile");
    }
  }, [user, history]);

  const [firstname, setFirstName] = useState(user && user.firstname);
  const [middlename, setMiddleName] = useState(user && user.middlename);
  const [lastname, setLastName] = useState(user && user.lastname);
  const [email, setEmail] = useState(user && user.email);
  const [dob, setDob] = useState(user && user.dob);
  const [gender, setGender] = useState(user && user.gender);
  const [state, setState] = useState(user && user.state);
  const [mobile, setMobile] = useState(user && user.mobile);
  const [address, setAddress] = useState(user && user.address);
  const [description, setDescription] = useState(user && user.description);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateBasicInfo(
        firstname,
        middlename,
        lastname,
        email,
        dob,
        gender,
        state,
        mobile,
        address,
        description
      )
    );
  };

  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && (
        <div className="alert alert-success">Basic Information Saved</div>
      )}
      {loading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={submitHandler}>
            <div className={`row`}>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>First Name</label>
                <input
                  type="text"
                  className="form-control"
                  value={firstname}
                  onChange={(e) => setFirstName(e.target.value)}
                  placeholder="Enter First Name"
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Middle Name</label>
                <input
                  type="text"
                  className="form-control"
                  value={middlename}
                  onChange={(e) => setMiddleName(e.target.value)}
                  placeholder="Enter Middle Name"
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Last Name</label>
                <input
                  type="text"
                  className="form-control"
                  value={lastname}
                  onChange={(e) => setLastName(e.target.value)}
                  placeholder="Enter Last Name"
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Email Address</label>
                <input
                  type="email"
                  className="form-control"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Enter Email Address"
                  readOnly
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Date of Birth</label>
                <input
                  type="date"
                  className="form-control"
                  value={dob}
                  onChange={(e) => setDob(e.target.value)}
                  placeholder="Enter Date of Birth"
                />
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Gender</label>
                <select
                  className="form-control"
                  value={gender}
                  onChange={(e) => setGender(e.target.value)}
                >
                  <option>{gender ? gender : "Select Gender"}</option>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>
                </select>
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>State of Origin</label>
                <select
                  className="form-control"
                  value={state}
                  onChange={(e) => setState(e.target.value)}
                >
                  <option>{state ? state : "Select....."}</option>
                  {states.map((item) => (
                    <option value={item} key={item}>
                      {item}
                    </option>
                  ))}
                </select>
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Mobile Number</label>
                <input
                  type="tel"
                  className="form-control"
                  value={mobile}
                  onChange={(e) => setMobile(e.target.value)}
                  placeholder="Enter Mobile"
                />
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label>Address</label>
                <textarea
                  className="form-control"
                  onChange={(e) => setAddress(e.target.value)}
                  placeholder="Enter Mobile"
                >
                  {address}
                </textarea>
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label>Brief Description</label>
                <textarea
                  className="form-control"
                  onChange={(e) => setDescription(e.target.value)}
                  placeholder="Tell us about yourself"
                >
                  {description}
                </textarea>
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label />
                <input
                  type="submit"
                  className="btn btn-dark form-control"
                  value="Save"
                />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default BasicInfo;
