import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getAllStaffs } from "../actions/adminActions";
import Navigation from "../components/Navbar";
import TopNavbar from "../components/Navbar/TopNavbar";
import styles from "./styles.module.css";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import Loading from "../components/UI/Loading";

const Users = () => {
  const dispatch = useDispatch();

  const lbanStaff = useSelector((state) => state.lbanStaff);
  const { loading, error, staffs } = lbanStaff;

  useEffect(() => {
    dispatch(getAllStaffs());
  }, [dispatch]);
  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <Navigation staff={styles.active} />
        <div className={styles.appContents}>
          <TopNavbar title="Staffs" />
          {error ? (
            <div className="alert alert-info">{error}</div>
          ) : (
            <>
              {loading ? (
                <Loading />
              ) : (
                <div className={styles.contents}>
                  <ReactHTMLTableToExcel
                    id="test-table-xls-button"
                    className="download-table-xls-button btn btn-info"
                    table="table-to-xls"
                    filename="Staff Database"
                    sheet="tablexls"
                    buttonText="Download as XLS"
                  />{" "}
                  <br />
                  <br />
                  <table className="table table-bordered" id="table-to-xls">
                    <thead>
                      <th>First Name</th>
                      <th>Middle Name</th>
                      <th>Last Name</th>
                      <th>Date of Birth</th>
                      <th>Gender</th>
                      <th>Department</th>
                      <th>CUG</th>
                      <th>NIN</th>
                      <th>BVN</th>
                      <th>Bank</th>
                      <th>Account Number</th>
                      <th>Account Name</th>
                    </thead>
                    <tbody>
                      {staffs &&
                        staffs.map((staff) => (
                          <tr key={staff.id}>
                            <td>{staff.firstname}</td>
                            <td>{staff.middlename}</td>
                            <td>{staff.lastname}</td>
                            <td>{staff.dob}</td>
                            <td>{staff.gender}</td>
                            <td>{staff.department}</td>
                            <td>{staff.cug}</td>
                            <td>{staff.nin}</td>
                            <td>{staff.bvn}</td>
                            <td>{staff.bank}</td>
                            <td>{staff.accountNumber}</td>
                            <td>{staff.accountName}</td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Users;
