import React from 'react'
import ui from './ui.module.css'

const Card = (props) => {
    return (
        <div className={ui.card}>
            <div className={ui.icons}>
                <div className={`${ui.icon} ${props.color}`}>
                    <h5>{props.children}</h5>
                </div>
            </div>
            <div className={ui.text}>
                <small>{props.name}</small>
                <h5>{props.score}</h5>
            </div>
        </div>
    )
}

export default Card
