import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "../screens/styles.module.css";
import { updateOInfo } from "../actions/profileActions";
import Loading from "./UI/Loading";
import { findmanager, findDept } from "../actions/userActions";

const BasicInfo = ({ user, history }) => {
  const dispatch = useDispatch();
  const oinfo = useSelector((state) => state.oinfo);
  const { loading, error, success } = oinfo;

  // const managers = useSelector((state) => state.managers);
  // const { lbanmanager } = managers;

  const departments = useSelector((state) => state.departments);
  const { lbandept } = departments;

  useEffect(() => {
    if (!user) {
      history.push("/profile");
    }
    dispatch(findmanager());
    dispatch(findDept());
  }, [user, history, dispatch]);

  const [department, setDepartment] = useState(user && user.department);
  const [position, setPosition] = useState(user && user.position);
  //   const [manager, setManager] = useState(user && user.manager);
  const [resumptionDate, setResumptionDate] = useState(
    user && user.resumptionDate
  );
  const [location, setLocation] = useState(user && user.location);
  const [cug, setCUG] = useState(user && user.cug);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(updateOInfo(department, position, resumptionDate, location, cug));
  };

  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && (
        <div className="alert alert-success">Official Information Saved</div>
      )}
      {loading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={submitHandler}>
            <div className={`row`}>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Department</label>
                <select
                  className="form-control"
                  value={department}
                  onChange={(e) => setDepartment(e.target.value)}
                >
                  <option>{department ? department : "Select....."}</option>
                  {lbandept.map((items) => (
                    <option value={items.name} key={items._id}>
                      {items.name}
                    </option>
                  ))}
                </select>
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Job Role</label>
                <input
                  type="text"
                  className="form-control"
                  value={position}
                  onChange={(e) => setPosition(e.target.value)}
                />
              </div>
              <div className={`col-md-12 ${styles.formPadding}`}>
                <label>CUG Number</label>
                <input
                  type="tel"
                  className="form-control"
                  value={cug}
                  onChange={(e) => setCUG(e.target.value)}
                />
              </div>
              {/* <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Manager</label>
                <select
                  className="form-control"
                  value={manager}
                  onChange={(e) => setManager(e.target.value)}
                >
                  <option>{manager ? manager : "Select....."}</option>
                  {lbanmanager.map((item) => (
                    <option value={item._id} key={item._id}>
                      {item.firstname + " " + item.lastname}
                    </option>
                  ))}
                </select>
              </div> */}

              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Location</label>
                <select
                  className="form-control"
                  value={location}
                  onChange={(e) => setLocation(e.target.value)}
                >
                  <option>{location ? location : "Select ....."}</option>
                  <option value="Lagos">Lagos</option>
                  <option value="Abuja">Abuja</option>
                </select>
              </div>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Resumption Date</label>
                <input
                  type="date"
                  className="form-control"
                  value={resumptionDate}
                  onChange={(e) => setResumptionDate(e.target.value)}
                />
              </div>

              <div className={`col-md-12 ${styles.formPadding}`}>
                <label />
                <input
                  type="submit"
                  className="btn btn-dark form-control"
                  value="Save"
                />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default BasicInfo;
