import {
  GET_ALL_STAFF_REQUEST,
  GET_ALL_STAFF_SUCCESS,
  GET_ALL_STAFF_FAIL,
} from "../constants/adminConstants";

export const getAllStaffReducer = (state = { staffs: [] }, action) => {
  switch (action.type) {
    case GET_ALL_STAFF_REQUEST:
      return { ...state, loading: true };
    case GET_ALL_STAFF_SUCCESS:
      return { loading: false, success: true, staffs: action.payload };
    case GET_ALL_STAFF_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
