import {
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGOUT,
  USER_VERIFY_EMAIL_REQUEST,
  USER_VERIFY_EMAIL_SUCCESS,
  USER_VERIFY_EMAIL_FAIL,
  USER_VERIFY_OTP_REQUEST,
  USER_VERIFY_OTP_SUCCESS,
  USER_VERIFY_OTP_FAIL,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_DETAILS_RESET,
  USER_DETAILS_FAIL,
  USER_PASSWORD_UPDATE_REQUEST,
  USER_PASSWORD_UPDATE_SUCCESS,
  USER_PASSWORD_UPDATE_FAIL,
  USER_PASSWORD_UPDATE_RESET,
  USER_VERIFY_OTP_RESET,
  USER_GET_MANAGER_FAIL,
  USER_GET_MANAGER_REQUEST,
  USER_GET_MANAGER_SUCCESS,
  USER_GET_DEPT_FAIL,
  USER_GET_DEPT_REQUEST,
  USER_GET_DEPT_SUCCESS,
  USER_ADD_ACCOUNT_FAIL,
  USER_ADD_ACCOUNT_REQUEST,
  USER_ADD_ACCOUNT_SUCCESS,
} from "../constants/userConstants";

export const userVerifyEmailReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_VERIFY_EMAIL_REQUEST:
      return { loading: true };
    case USER_VERIFY_EMAIL_SUCCESS:
      return { loading: false, emailVerification: action.payload };
    case USER_VERIFY_EMAIL_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const userVerifyOTPReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_VERIFY_OTP_REQUEST:
      return { loading: true };
    case USER_VERIFY_OTP_SUCCESS:
      return {
        loading: false,
        success: true,
        user: action.payload,
      };
    case USER_VERIFY_OTP_FAIL:
      return { loading: false, error: action.payload };
    case USER_VERIFY_OTP_RESET:
      return { loading: false, userInfo: action.payload };
    default:
      return state;
  }
};

export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return { loading: true };
    case USER_LOGIN_SUCCESS:
      return { loading: false, userInfo: action.payload };
    case USER_LOGIN_FAIL:
      return { loading: false, error: action.payload };
    case USER_LOGOUT:
      return {};
    default:
      return state;
  }
};

