import React from "react";
import { useSelector } from "react-redux";
import Navigation from "../components/Navbar";
import TopNavbar from "../components/Navbar/TopNavbar";
import { Link } from "react-router-dom";
import styles from "./styles.module.css";
import { GiPositionMarker, GiStopwatch, GiTrophyCup } from "react-icons/gi";
import avatar from "../assets/user.png";

const Profile = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  function calculateAge(dob, dt) {
    dt = dt || new Date();
    var diff = dt.getTime() - new Date(dob).getTime();
    return Math.floor(diff / (1000 * 60 * 60 * 24 * 365.25));
  }

  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <Navigation profile={styles.active} />
        <div className={styles.appContents}>
          <TopNavbar title="Profile" />
          <div className={styles.contents}>
            {/* User Description */}
            <div className={styles.profiles}>
              <div className={styles.profileGrid}>
                <div className={styles.profile}>
                  <div className={styles.photo}>
                    <img
                      src={user ? `/uploads/${user.photo}` : avatar}
                      alt={user ? user.firstname : ""}
                    />
                  </div>
                </div>
                <div className={styles.profile}>
                  <h5>
                    {user ? user.firstname : ""}&nbsp;{" "}
                    {user ? user.middlename : ""}&nbsp;{" "}
                    {user ? user.lastname : ""}
                  </h5>
                  <p className="text-info">
                    <GiPositionMarker /> {user ? user.location : ""} Nigeria
                  </p>
                  <small>
                    <b>Role:</b> {user ? user.position : ""}
                  </small>
                  <br />
                  <small>
                    <b>Department:</b> {user ? user.department : ""}
                  </small>
                  <br />
                  <small>
                    <b className="text-info">Manager:</b>{" "}
                    {user ? user.manager : ""}
                  </small>
                  <br />
                  <br />
                  <p>Brief Description</p>
                  <small>{user ? user.description : ""}</small>
                  <br />
                  <br />
                  <div className={styles.trophy}>
                    <div className={styles.icons}>
                      <GiStopwatch />
                    </div>
                    <div className={styles.text}>
                      <p>
                        {calculateAge(user ? user.resumptionDate : "")} year(s)
                        Job
                      </p>
                      <small>Experience at LBAN </small>
                    </div>
                  </div>
                  <div className={styles.trophy}>
                    <div className={styles.icons}>
                      <GiTrophyCup />
                    </div>
                    <div className={styles.text}>
                      <p>0 Certifications</p>
                      <small>Achieved</small>
                    </div>
                  </div>
                </div>
                <div className={styles.profile}>
                  <Link to="/edit/profile" className="btn btn-info">
                    Edit Profile
                  </Link>
                </div>
              </div>
            </div>
            {/* End of User Description*/}

            {/* Expereince */}
            <div></div>
            {/* End of Expereince */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
