import axios from "axios";
import {
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGOUT,
  //   USER_REGISTER_REQUEST,
  //   USER_REGISTER_SUCCESS,
  //   USER_REGISTER_FAIL,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_DETAILS_FAIL,
  USER_VERIFY_EMAIL_REQUEST,
  USER_VERIFY_EMAIL_SUCCESS,
  USER_VERIFY_EMAIL_FAIL,
  USER_VERIFY_OTP_REQUEST,
  USER_VERIFY_OTP_SUCCESS,
  USER_VERIFY_OTP_FAIL,
  USER_PASSWORD_UPDATE_REQUEST,
  USER_PASSWORD_UPDATE_SUCCESS,
  USER_PASSWORD_UPDATE_FAIL,
  USER_PASSWORD_UPDATE_RESET,
  USER_VERIFY_OTP_RESET,
  USER_DETAILS_RESET,
  USER_GET_MANAGER_FAIL,
  USER_GET_MANAGER_REQUEST,
  USER_GET_MANAGER_SUCCESS,
  USER_GET_DEPT_SUCCESS,
  USER_GET_DEPT_REQUEST,
  USER_GET_DEPT_FAIL,
} from "../constants/userConstants";

export const verify = (email) => async (dispatch) => {
  try {
    dispatch({ type: USER_VERIFY_EMAIL_REQUEST });

    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const { data } = await axios.post(
      "/api/v1/staff/login/addEmail",
      { email },
      config
    );

    dispatch({
      type: USER_VERIFY_EMAIL_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: USER_VERIFY_EMAIL_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

