import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  userLoginReducer,
  userVerifyEmailReducer,
  userDetailsReducer,
  userPasswordReducer,
  userVerifyOTPReducer,
  getManagerReducer,
  getDeptReducer,
  addAccountReducer,
} from "./reducers/userReducers";

import {
  userBasicInfoReducer,
  userEmergencyInfoReducer,
  userOfficeInfoReducer,
  userPhotoReducer,
} from "./reducers/profileReducers";
import { getAllStaffReducer } from "./reducers/adminReducers";

const reducer = combineReducers({
  userStatus: userVerifyEmailReducer,
  userLogin: userLoginReducer,
  userDetails: userDetailsReducer,
  passwordUpdate: userPasswordReducer,
  otpVerification: userVerifyOTPReducer,
  info: userBasicInfoReducer,
  einfo: userEmergencyInfoReducer,
  oinfo: userOfficeInfoReducer,
  ainfo: addAccountReducer,
  managers: getManagerReducer,
  departments: getDeptReducer,
  myaccount: addAccountReducer,
  photo: userPhotoReducer,
  lbanStaff: getAllStaffReducer,
});

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null;
const userDetailsFromStorage = localStorage.getItem("userDetails")
  ? JSON.parse(localStorage.getItem("user"))
  : null;

const initialState = {
  userLogin: { userInfo: userInfoFromStorage },
  userDetails: { user: userDetailsFromStorage },
  passwordUpdate: { userInfo: userInfoFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
