import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AccountContact from "./screens/AccountContact";
import EditProfile from "./screens/EditProfile";
import EmergencyContact from "./screens/EmergencyContact";
import Home from "./screens/Home";
import Login from "./screens/Login";
import OfficeContact from "./screens/OfficeContact";
import PhotoUpload from "./screens/Photo";
import Profile from "./screens/Profile";
import Register from "./screens/Register";
import UpdatePassword from "./screens/UpdatePassword";
import Users from "./screens/Users";
import Verify from "./screens/Verify";
import VerifyOTP from "./screens/VerifyOTP";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Register} />
        <Route path="/login" exact component={Login} />
        <Route path="/users" exact component={Users} />
        <Route path="/auth" exact component={Verify} />
        <Route path="/verifyOTP" exact component={VerifyOTP} />
        <Route path="/update/password" exact component={UpdatePassword} />
        <Route path="/dashboard" exact component={Home} />
        <Route path="/staffs" exact component={Users} />
        <Route path="/profile" exact component={Profile} />
        <Route path="/edit/profile" exact component={EditProfile} />
        <Route path="/edit/officeInfo" exact component={EditProfile} />
        <Route
          path="/edit/profile/emergency-info"
          exact
          component={EmergencyContact}
        />
        <Route
          path="/edit/profile/office-info"
          exact
          component={OfficeContact}
        />
        <Route
          path="/edit/profile/account-info"
          exact
          component={AccountContact}
        />
        <Route
          path="/edit/profile/photo-upload"
          exact
          component={PhotoUpload}
        />
      </Switch>
    </Router>
  );
}

export default App;
